import React, { FC, useRef, useState } from 'react';
import {
  GeneralSearchContainer,
  GeneralSearchWrapper,
  SearchIconContainer,
  SearchResults,
  SearchResultsContainer,
  StyledGeneralSearch,
} from './styled';
import SearchIcon from '../Icons/SearchIcon/SearchIcon';
import { TextInput } from '../Form';
import { AnimatePresence, motion } from 'framer-motion';
import useOnClickOutside from '../../../hooks/useClickOutside';

export const GeneralSearch: FC = () => {
  const [isSearchInputVisible, setIsSearchInputVisible] = useState(false);
  const handleSearchClick = () => {
    setIsSearchInputVisible(true);
    if (!isSearchInputVisible)
      setTimeout(() => {
        (document.querySelector('#general-search-input') as HTMLInputElement)?.focus();
      }, 0);
  };

  const searchRef = useRef(null);
  useOnClickOutside(searchRef, () => setIsSearchInputVisible(false));

  return (
    <GeneralSearchWrapper ref={searchRef}>
      <GeneralSearchContainer>
        <StyledGeneralSearch>
          {!isSearchInputVisible && (
            <SearchIconContainer onClick={handleSearchClick}>
              <SearchIcon></SearchIcon>
            </SearchIconContainer>
          )}
          <AnimatePresence>
            {isSearchInputVisible && (
              <motion.div initial={{ width: 0 }} animate={{ width: 250 }}>
                <TextInput
                  placeholder="Search"
                  id="general-search-input"
                  prefixContent={<SearchIcon />}
                  onClear={() => setIsSearchInputVisible(false)}
                ></TextInput>
              </motion.div>
            )}
          </AnimatePresence>
          {isSearchInputVisible && (
            <SearchResultsContainer
              initial={{ width: 0, height: 0 }}
              animate={{ width: 400, height: 350 }}
            >
              <SearchResults>Test</SearchResults>
            </SearchResultsContainer>
          )}
        </StyledGeneralSearch>
      </GeneralSearchContainer>
    </GeneralSearchWrapper>
  );
};
